<style scoped>
  .label {
    color: black;
    display: block;
    line-height: 30px;
  }

  .value {
    margin-left: 10px;
    line-height: 1rem;
    color: #666;
  }
</style>

<template>
  <div>
    <Card>
      <h2>问卷调查：这是一个标题</h2>
      <Row style="margin: 20px">
        <Col :span="12">
          <span class="label">创建人: <span class="value">{{this.data.publisher}}</span></span>
          <span class="label">创建时间: <span class="value">{{ this.data.create_time }}</span></span>
        </Col>
        <Col :span="12" style="text-align: right;">
          <span class="value" style="display: block;">参与人数 </span>
          <h2 style="margin-top: 10px">{{this.data.count}}</h2>
        </Col>
      </Row>
      <Spin size="large" fix v-if="load"></Spin>

    </Card>
  </div>
</template>

<script>
import api from './api'

export default {
  data () {
    return {
      load: false,
      data: null
    }
  },
  mounted () {
    this.loadData()
  },
  methods: {
    loadData () {
      this.load = true
      api.statics({ id: this.$route.params.id }, res => {
        this.load = false
        this.data = res.data.data
      }, () => {
        this.load = false
      })
    }
  }
}
</script>
